body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s, color 0.3s;
}

body[data-theme='light'] {
  background-color: #ffffff;
  color: #000000;
}

body[data-theme='dark'] {
  background-color: #1a1a1a;
  color: #ffffff;
}

/* Add these new rules */
[data-theme='light'] .App {
  background-color: #ffffff;
  color: #000000;
}

[data-theme='dark'] .App {
  background-color: #1a1a1a;
  color: #ffffff;
}

/* Style for links */
a {
  color: inherit;
  text-decoration: none;
}

/* Style for buttons */
button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s;
}

[data-theme='dark'] button {
  background-color: #45a049;
}

/* Add styles for other common elements as needed */

nav ul {
  display: flex;
  list-style-type: none;
  padding: 0;
}

nav ul li {
  margin-right: 1rem;
}

nav ul li a {
  text-decoration: none;
  color: inherit;
}
